"use client";

import { Event, User } from "@/types";
import { Card } from "./ui/card";
import { ScrollArea } from "./ui/scroll-area";
import { formatDistanceToNow } from "date-fns";
import { fr } from "date-fns/locale";

interface EventsListProps {
  events: Event[];
  selectedUser: User | null;
}

export function EventsList({ events, selectedUser }: EventsListProps) {
  return (
    <Card className="p-6 bg-black/10 backdrop-blur-lg border-none">
      <h2 className="text-2xl font-bold mb-4 text-white">
        ÃvÃ©nements RÃ©cents
      </h2>
      
      <ScrollArea className="h-[200px] w-full rounded-md pr-4">
        <div className="space-y-3">
          {events.map((event) => {
            const isHighlighted = selectedUser && event.userId === selectedUser.id;
            
            return (
              <div
                key={event.id}
                className={`
                  p-3 rounded-lg transition-all duration-200
                  ${isHighlighted 
                    ? 'bg-gradient-to-r from-cyan-500/20 to-blue-500/20 border border-cyan-500/30' 
                    : 'bg-white/5'}
                `}
              >
                <p className={`
                  text-sm mb-1
                  ${isHighlighted ? 'text-cyan-300' : 'text-gray-300'}
                `}>
                  {event.text}
                </p>
                <p className="text-xs text-gray-500">
                  {formatDistanceToNow(event.timestamp, {
                    addSuffix: true,
                    locale: fr
                  })}
                </p>
              </div>
            );
          })}
        </div>
      </ScrollArea>
    </Card>
  );
}
