"use client";

import { User } from "@/types";
import { Card } from "./ui/card";
import { ScrollArea } from "./ui/scroll-area";

interface UserListProps {
  users: User[];
  selectedUserId: string | null;
  onSelectUser: (userId: string) => void;
}

export function UserList({ users, selectedUserId, onSelectUser }: UserListProps) {
  return (
    <Card className="p-4 bg-black/10 backdrop-blur-lg border-none">
      <h2 className="text-2xl font-bold mb-4 text-white">Utilisateurs (RFID)</h2>
      <ScrollArea className="h-[200px] w-full rounded-md">
        <div className="space-y-2">
          {users.map((user) => (
            <div
              key={user.id}
              onClick={() => onSelectUser(user.id)}
              className={`
                p-3 rounded-lg cursor-pointer transition-all duration-200
                ${selectedUserId === user.id 
                  ? 'bg-gradient-to-r from-cyan-500 to-blue-500 text-white shadow-lg scale-105' 
                  : 'bg-white/10 hover:bg-white/20 text-gray-100'}
              `}
            >
              <div className="font-semibold">{user.name}</div>
              <div className="text-sm opacity-75">ID: {user.id}</div>
            </div>
          ))}
        </div>
      </ScrollArea>
    </Card>
  );
}
