"use client";

import { useState, useEffect } from "react";
import { User, Score, Event } from "@/types";
import { UserList } from "@/components/user-list";
import { ProfileCard } from "@/components/profile-card";
import { ScoreInput } from "@/components/score-input";
import { EventsList } from "@/components/events-list";
import { ScoreChart } from "@/components/score-chart";

// Dummy data
const initialUsers: User[] = [
  { id: "RFID1234", name: "Alice" },
  { id: "RFID5678", name: "Bob" },
  { id: "RFID9123", name: "Charlie" },
];

const initialScores: Score[] = [
  { user: "Alice", hits: 8, misses: 2, time: new Date(Date.now() - 60000 * 5) },
  { user: "Bob", hits: 10, misses: 0, time: new Date(Date.now() - 60000 * 4) },
  { user: "Alice", hits: 7, misses: 3, time: new Date(Date.now() - 60000 * 3) },
];

const initialEvents: Event[] = [
  { id: 1, text: "SystÃ¨me initialisÃ©", timestamp: new Date(), userId: null },
  { 
    id: 2, 
    text: "Alice a tirÃ© Ã  10:02:15", 
    timestamp: new Date(Date.now() - 60000 * 5),
    userId: "RFID1234"
  },
  {
    id: 3,
    text: "Bob a tirÃ© Ã  10:02:45",
    timestamp: new Date(Date.now() - 60000 * 4),
    userId: "RFID5678"
  },
];

export default function Home() {
  const [users] = useState<User[]>(initialUsers);
  const [scores, setScores] = useState<Score[]>(initialScores);
  const [events, setEvents] = useState<Event[]>(initialEvents);
  const [selectedUserId, setSelectedUserId] = useState<string | null>(null);

  const selectedUser = users.find(u => u.id === selectedUserId) || null;

  const handleScoreSubmit = (hits: number, misses: number) => {
    if (!selectedUser) return;

    const newScore: Score = {
      user: selectedUser.name,
      hits,
      misses,
      time: new Date(),
    };

    const newEvent: Event = {
      id: events.length + 1,
      text: `${selectedUser.name} a marquÃ© ${hits} tirs rÃ©ussis et ${misses} tirs ratÃ©s`,
      timestamp: new Date(),
      userId: selectedUser.id,
    };

    setScores(prev => [...prev, newScore]);
    setEvents(prev => [newEvent, ...prev]);
  };

  // Simulate real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      if (users.length > 0) {
        const randomUser = users[Math.floor(Math.random() * users.length)];
        const hits = Math.floor(Math.random() * 11);
        const misses = 10 - hits;

        const newScore: Score = {
          user: randomUser.name,
          hits,
          misses,
          time: new Date(),
        };

        const newEvent: Event = {
          id: events.length + 1,
          text: `${randomUser.name} a marquÃ© ${hits} tirs rÃ©ussis et ${misses} tirs ratÃ©s`,
          timestamp: new Date(),
          userId: randomUser.id,
        };

        setScores(prev => [...prev, newScore]);
        setEvents(prev => [newEvent, ...prev.slice(0, 49)]);
      }
    }, 3000);

    return () => clearInterval(interval);
  }, [users, events.length]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-gray-900">
      <header className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 p-6 shadow-lg">
        <h1 className="text-3xl font-bold text-white text-center">
          Dashboard Tir Sportif
        </h1>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Column */}
          <div className="lg:col-span-1 space-y-6">
            <UserList
              users={users}
              selectedUserId={selectedUserId}
              onSelectUser={setSelectedUserId}
            />
            <ProfileCard
              user={selectedUser}
              scores={scores}
            />
            <ScoreInput
              user={selectedUser}
              onScoreSubmit={handleScoreSubmit}
            />
            <EventsList
              events={events}
              selectedUser={selectedUser}
            />
          </div>

          {/* Right Column */}
          <div className="lg:col-span-2">
            <ScoreChart
              user={selectedUser}
              scores={scores}
            />
          </div>
        </div>
      </main>

      <footer className="w-full bg-black/20 text-gray-400 text-center py-4 mt-8">
        ConnectÃ© via Wi-Fi - DonnÃ©es mises Ã  jour toutes les 3 secondes
      </footer>
    </div>
  );
}
