"use client";

import { User, Score } from "@/types";
import { Card } from "./ui/card";
import { formatDistanceToNow } from "date-fns";
import { fr } from "date-fns/locale";

interface ProfileCardProps {
  user: User | null;
  scores: Score[];
}

export function ProfileCard({ user, scores }: ProfileCardProps) {
  if (!user) {
    return (
      <Card className="p-6 bg-black/10 backdrop-blur-lg border-none">
        <p className="text-gray-300 text-center">
          SÃ©lectionnez un joueur pour voir son profil
        </p>
      </Card>
    );
  }

  const userScores = scores.filter((s) => s.user === user.name);
  const totalHits = userScores.reduce((sum, score) => sum + score.hits, 0);
  const totalMisses = userScores.reduce((sum, score) => sum + score.misses, 0);
  const kdRatio = totalHits + totalMisses > 0 
    ? (totalHits / (totalHits + totalMisses)).toFixed(2) 
    : "N/A";

  const lastScore = userScores[userScores.length - 1];

  return (
    <Card className="p-6 bg-black/10 backdrop-blur-lg border-none text-white">
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-2xl font-bold bg-gradient-to-r from-cyan-400 to-blue-400 bg-clip-text text-transparent">
            {user.name}
          </h2>
          <span className="px-3 py-1 rounded-full bg-cyan-500/20 text-cyan-300 text-sm">
            {user.id}
          </span>
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-1">
            <p className="text-sm text-gray-400">Total Tirs</p>
            <p className="text-2xl font-bold text-cyan-400">
              {userScores.length}
            </p>
          </div>
          <div className="space-y-1">
            <p className="text-sm text-gray-400">Ratio K/D</p>
            <p className="text-2xl font-bold text-cyan-400">{kdRatio}</p>
          </div>
          <div className="space-y-1">
            <p className="text-sm text-gray-400">Tirs RÃ©ussis</p>
            <p className="text-2xl font-bold text-green-400">{totalHits}</p>
          </div>
          <div className="space-y-1">
            <p className="text-sm text-gray-400">Tirs RatÃ©s</p>
            <p className="text-2xl font-bold text-red-400">{totalMisses}</p>
          </div>
        </div>

        {lastScore && (
          <div className="mt-4 p-3 rounded-lg bg-white/5">
            <p className="text-sm text-gray-400">Dernier Score</p>
            <p className="text-cyan-400">
              {lastScore.hits} rÃ©ussis, {lastScore.misses} ratÃ©s
            </p>
            <p className="text-xs text-gray-500">
              {formatDistanceToNow(lastScore.time, { 
                addSuffix: true,
                locale: fr 
              })}
            </p>
          </div>
        )}
      </div>
    </Card>
  );
}
