"use client";

import { useState } from "react";
import { Card } from "./ui/card";
import { Button } from "./ui/button";
import { User } from "@/types";

interface ScoreInputProps {
  user: User | null;
  onScoreSubmit: (hits: number, misses: number) => void;
}

export function ScoreInput({ user, onScoreSubmit }: ScoreInputProps) {
  const [selectedHits, setSelectedHits] = useState<number | null>(null);
  const MAX_SHOTS = 10;

  const handleHitSelection = (hits: number) => {
    setSelectedHits(hits);
  };

  const handleSubmit = () => {
    if (selectedHits !== null) {
      onScoreSubmit(selectedHits, MAX_SHOTS - selectedHits);
      setSelectedHits(null);
    }
  };

  return (
    <Card className="p-6 bg-black/10 backdrop-blur-lg border-none">
      <h2 className="text-2xl font-bold mb-4 text-white">
        Saisir un score
      </h2>
      
      <div className="space-y-6">
        <div className="space-y-3">
          <p className="text-gray-300">Tirs rÃ©ussis (Hits)</p>
          <div className="grid grid-cols-6 gap-2">
            {Array.from({ length: MAX_SHOTS + 1 }).map((_, i) => (
              <Button
                key={i}
                variant={selectedHits === i ? "default" : "outline"}
                onClick={() => handleHitSelection(i)}
                className={`
                  h-12 text-lg font-bold transition-all duration-200
                  ${selectedHits === i 
                    ? 'bg-gradient-to-r from-cyan-500 to-blue-500 text-white scale-110' 
                    : 'bg-black/20 hover:bg-black/30 text-gray-300'}
                `}
              >
                {i}
              </Button>
            ))}
          </div>
        </div>

        <div className="space-y-2">
          <p className="text-gray-300">Tirs ratÃ©s (Misses)</p>
          <div className="text-2xl font-bold text-cyan-400">
            {selectedHits !== null ? MAX_SHOTS - selectedHits : '-'}
          </div>
        </div>

        <Button
          onClick={handleSubmit}
          disabled={selectedHits === null || !user}
          className="w-full h-12 text-lg font-bold bg-gradient-to-r from-cyan-500 to-blue-500 
                     hover:from-cyan-600 hover:to-blue-600 transition-all duration-300
                     disabled:opacity-50 disabled:cursor-not-allowed"
        >
          Ajouter Score
        </Button>

        {!user && (
          <p className="text-sm text-gray-400 text-center">
            SÃ©lectionnez un joueur pour ajouter un score
          </p>
        )}
      </div>
    </Card>
  );
}
