"use client";

import { User, Score } from "@/types";
import { Card } from "./ui/card";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

interface ScoreChartProps {
  user: User | null;
  scores: Score[];
}

export function ScoreChart({ user, scores }: ScoreChartProps) {
  const userScores = user 
    ? scores.filter(s => s.user === user.name).slice(-20)
    : [];

  const data = {
    labels: userScores.map(s => 
      new Date(s.time).toLocaleTimeString('fr-FR', {
        hour: '2-digit',
        minute: '2-digit'
      })
    ),
    datasets: [
      {
        label: 'Tirs rÃ©ussis',
        data: userScores.map(s => s.hits),
        fill: true,
        backgroundColor: 'rgba(0, 255, 209, 0.2)',
        borderColor: 'rgb(0, 255, 209)',
        tension: 0.4,
        pointRadius: 6,
        pointHoverRadius: 8,
        pointBackgroundColor: 'rgb(0, 255, 209)',
        pointBorderColor: 'rgb(0, 255, 209)',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgb(0, 255, 209)'
      }
    ]
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      y: {
        beginAtZero: true,
        max: 10,
        grid: {
          color: 'rgba(255, 255, 255, 0.1)'
        },
        ticks: {
          color: 'rgba(255, 255, 255, 0.7)',
          font: {
            weight: 'bold'
          }
        }
      },
      x: {
        grid: {
          color: 'rgba(255, 255, 255, 0.1)'
        },
        ticks: {
          color: 'rgba(255, 255, 255, 0.7)',
          font: {
            weight: 'bold'
          }
        }
      }
    },
    plugins: {
      legend: {
        labels: {
          color: 'rgba(255, 255, 255, 0.9)',
          font: {
            weight: 'bold'
          }
        }
      },
      tooltip: {
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        titleColor: 'rgb(0, 255, 209)',
        bodyColor: '#fff',
        borderColor: 'rgb(0, 255, 209)',
        borderWidth: 1,
        padding: 12,
        displayColors: false
      }
    }
  };

  return (
    <Card className="p-6 bg-black/10 backdrop-blur-lg border-none h-full">
      <h2 className="text-2xl font-bold mb-6 text-white">
        {user ? `Scores de ${user.name}` : 'SÃ©lectionnez un joueur'}
      </h2>
      
      <div className="h-[400px] w-full">
        {user ? (
          userScores.length > 0 ? (
            <Line data={data} options={options} />
          ) : (
            <div className="h-full flex items-center justify-center text-gray-400">
              Aucun score enregistrÃ© pour ce joueur
            </div>
          )
        ) : (
          <div className="h-full flex items-center justify-center text-gray-400">
            SÃ©lectionnez un joueur pour voir ses scores
          </div>
        )}
      </div>
    </Card>
  );
}
